* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  color: #333;
  font-size: .14rem;
  box-sizing: border-box;
}

html {
  font-size: 100px;
}

body {
  user-select: none;
  -webkit-user-select: none;
  touch-callout: none;
  -webkit-touch-callout: none;
}

a {
  tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
}

html,
body,
#root,
.wrap {
  width: 100%;
  height: 100%;
}

.wrap {
  display: flex;
  flex-direction: column;
}

header {
  width: 100%;
  height: 60px;
  display: flex;
  background: #2B5295;
  align-items: center;
}

header nav {
  flex: 1;
  height: 100%;
  display: flex;
  margin-left: .4rem;
}

header nav a {
  flex: 1;
  height: 100%;
  line-height: 60px;
  text-align: center;
  color: #fff;
}

header nav a.action {
  background: #1E3D73;
}

header span {
  color: #fff;
  margin-right: .2rem;
}

section {
  flex: 1;
  background: #F7F7F7;
}

section .shou {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  margin-top: .2rem;
}

section .shou .left {
  flex: 3;
  text-align: center;
}

section .shou .right {
  flex: 7;
  display: flex;
  flex-wrap: wrap;
}

section .shou .right .right-block {
  width: 46%;
  background: #fff;
  margin: .1rem;
  border-radius: .2rem;
}

section .shou .right .right-block div {
  display: flex;
  flex-direction: column;
  padding: 0 .1rem;
}

section .shou .right .right-block div h3 {
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

section .shou .right .right-block div dl {
  display: flex;
  margin-top: .2rem;
  align-items: center;
}

section .shou .right .right-block div dl dt {
  flex: 4;
}

section .shou .right .right-block div dl dt img {
  width: 100%;
  vertical-align: middle;
}

section .shou .right .right-block div dl dd {
  flex: 6;
}

section .shou .right .right-block div dl dd ul {
  display: flex;
  flex-wrap: wrap;
}

section .shou .right .right-block div dl dd ul li {
  width: 50%;
  height: .4rem;
  line-height: .4rem;
  text-align: center;
  color: #5BAAEF;
}

section .shou button {
  position: fixed;
  left: .2rem;
  bottom: .2rem;
  border: none;
  background: skyblue;
  color: #fff;
  padding: .1rem .2rem;
  border-radius: .1rem;
}

section .content {
  width: 100%;
  height: 100%;
  display: flex;
}

section .content .nav {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding-left: .3rem;
  background: #C5CFEA;
  padding-top: .2rem;
}

section .content .nav a {
  height: .4rem;
  line-height: .4rem;
  color: #7183A9;
  font-size: .16rem;
  position: relative;
}

section .content .nav a.action {
  color: #0156FF;
}

section .content .nav a.action:before {
  content: '';
  position: absolute;
  top: .1rem;
  left: -.1rem;
  width: .02rem;
  height: .2rem;
  background: #0156FF;
}

section .content .right-content {
  flex: 9;
}
